import React, {useEffect, useState} from 'react'
import styled from 'styled-components'
import { Text } from '@pancakeswap/uikit'
import { ethers } from "ethers";
import { useWeb3React } from "@web3-react/core";
import { VStack } from '../../Swap/components/Stacks'
import { useChonk } from "../../../hooks/useContract";

const Container = styled.div`
  border-radius: 16px;
  margin: 20px;
  background-color: ${({ theme }) => theme.colors.input};
  box-shadow: ${({ theme }) => theme.shadows.inset};
`

const EntryWrapper = styled.div`
`

interface EntryProps {
  label: string
  value: string
  isBnbAndHasUsdtValue: boolean
  usdtValue?: string
}
function Entry({
  label,
  value,
  isBnbAndHasUsdtValue = false,
  usdtValue
}: EntryProps) {
  return (
    <EntryWrapper>
      <Text bold>{label}:</Text>
      <Text>{value}{isBnbAndHasUsdtValue ? ` BNB (~ ${usdtValue} USDT)` : ""}</Text>
    </EntryWrapper>
  )
}

export default function PlatformStatistics() {
  const [totalUsers, setTotalUsers] = useState("0");
  const [totalValueLocked, setTotalValueLocked] = useState("0");
  const chonkContract = useChonk();
  const { account } = useWeb3React()

  useEffect(() => {
    const getData = async () => {
      const promises = [
        chonkContract.totalBnbBalance(),
        chonkContract.getTokenHolderAmount()
      ];
      await Promise.all(promises).then((values) => {
        setTotalValueLocked(Number(ethers.utils.formatEther(BigInt(values[0]._hex))).toFixed(2));
        setTotalUsers(BigInt(values[1]._hex).toString());
      });
    }
    if(account) {
      getData();
    }
  })

  return (
    <Container>
      <VStack spacing="20" style={{margin: "20px"}}>
        <Text bold fontSize="20px">Statistics</Text>
        <Entry label="Total Users" value={totalUsers} isBnbAndHasUsdtValue={false}/>
        <Entry label="Total Value Locked" value={totalValueLocked} isBnbAndHasUsdtValue usdtValue="0"/>
      </VStack>
    </Container>
  )
}
